<template>
    <section data-v-fadetail>
        <!-- 画面ヘッダ -->
        <ViewHeader 
            v-bind:moduleName   =   'moduleName'
            v-bind:viewName     =   'viewName'
        /> 

        <!-- 家計簿ヘッダ -->
        <section>
            <div class='entry' data-v-fadetail>
                <form data-v-fadetail>
                    <div class='entryInputBlock' data-v-fadetail>
                        <p class='formLabel' data-v-fadetail>年</p>
                        <input readonly type="number" name="year" v-model="faHeader.year" data-v-fadetail>
                    </div>
                    <div class='entryInputBlock' data-v-fadetail>
                        <p class='formLabel' data-v-fadetail>月</p>
                        <input readonly type="number" name="month" v-model="faHeader.month" data-v-fadetail>
                    </div>
                    <div class='entryInputBlock' data-v-fadetail>
                        <p class='formLabel' data-v-fadetail>日</p>
                        <input readonly type="number" name="day" v-model="faHeader.day" data-v-fadetail>
                    </div>
                    <div class='entryInputBlock' data-v-fadetail>
                        <p class='formLabel' data-v-fadetail>口座ID</p>
                        <input readonly type="number" name="day" v-model="faHeader.bank" data-v-fadetail>
                    </div>
                </form>
            </div>
        </section>

        <FAentry
            v-for='(entry, index) in faEntrys.filter(function(entry){return entry.temp == false})' :key='index'
            v-bind:index = 'index'
            v-bind:entry = 'entry'
            v-bind:accId = 'accId'
        />

        <!-- ヘッダ入力部 -->
        <section>
            <div class='entry' data-v-fadetail>
                <form data-v-fadetail>
                    <div class='entryInputBlock' data-v-fadetail>
                        <p class='formLabel' data-v-fadetail>外税・手数料等</p>
                        <input type='number' name='hExtInput' v-model="faHeader.hExt">
                    </div>
                    <div class='entryInputBlock' data-v-fadetail>
                        <p class='formLabel' data-v-fadetail>値引額</p>
                        <input type='number' name='hDiscountInput' v-model="faHeader.hDiscount">
                    </div>
                    <div class='entryInputBlock' data-v-fadetail>
                        <p class='formLabel' data-v-fadetail>コメント</p>
                        <input type='text' name='hCommentInput' class='commentInput'  v-model="faHeader.hComment" data-v-fadetail>
                    </div>
                    <div class='entryInputBlock' data-v-fadetail>
                        <p class='formLabel' data-v-fadetail>伝票合計金額</p>
                        <input readonly type="number" name="year" v-model="faHeader.total" data-v-fadetail>
                    </div>
                </form>
            </div>
        </section>

        <!-- 伝票取得失敗モーダル -->
        <Modal
            v-show="errorModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event = "postErrorModal"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                <p>家計簿伝票：{{faHeader.faNo}}の取得に失敗しました。</p>
                <p>(コード：{{errorCode}})</p>
            </template>
        </Modal>

    </section>
</template>

<script>
import axios    from 'axios';
import Cookies  from 'js-cookie';

import apiUrls  from '../mixins/ApiUrlMixin'

import ViewHeader   from '../components/ViewHeader'
import FAheader     from '../components/FAheader'
import FAentry      from '../components/FAentry'
import Modal        from '../components/Modal'
import Loading      from '../components/Loading'

export default {
    name : 'FAdetail',
    mixins : [apiUrls],

    // ------------------------------------
    //  使用コンポーネント一覧
    // ------------------------------------
    components : {
        ViewHeader,
        FAentry,
        FAheader,
        Modal,
        Loading
    },

    // ------------------------------------
    //  初期化処理
    // ------------------------------------
    created(){
        // 伝票番号取得
        let urlParams = new URLSearchParams(window.location.search)
        this.faHeader.faNo = urlParams.get('faNo')
        this.viewName = this.viewName + this.faHeader.faNo

        //  科目取得処理
        // トークン取得
        let token = Cookies.get('token')
        if(token == null)
        {
            return
        }

        // thisを格納
        const self = this

        // 科目取得API call
        axios.get(this.apiUrls["getAccounts"],
        {
            headers : {
                'Authorization' : token
            }
        })
        .then(function(response){
            // Http status not 200
            if(response.status != 200)
            {
                return
            }
            // Auth failed
            if(response.data.status != 'S')
            {
                return
            }
            
            // 取得成功時
            for(let i=0; i<response.data.data.length; i++)
            {
                // テンプレート用オブジェクトをコピー
                let data = Object.assign({}, self.accId[0])
                
                // 値設定
                data.id     = response.data.data[i].accid
                data.name   = response.data.data[i].name
                data.type   = response.data.data[i].accType
                // 明細オブジェクト追加
                self.accId.push(data)
            }

            // テンプレート用オブジェクト削除
            self.accId.shift()
        })
        .catch(function(error){
            self.errorModal = true
            self.errorCode  = 9999
            return
        })

        // 家計簿明細取得
        let faGetUrl = this.apiUrls["getDetail"] + '/' + this.faHeader.faNo
        axios.get(faGetUrl,
        {
            headers : {
                'Authorization' : token
            }
        })
        .then(function(response){
            // Http status not 200
            if(response.status != 200)
            {
                self.errorModal = true
                self.errorCode  = 9999
                return
            }
            // Auth failed
            if(response.data.status != 'S')
            {
                self.errorModal = true
                self.errorCode  = response.data.code
                return
            }

            // ヘッダ部設定
            self.faHeader.year = response.data.data.header.year
            self.faHeader.month = response.data.data.header.month
            self.faHeader.day = response.data.data.header.day
            self.faHeader.bank = response.data.data.header.bank
            self.faHeader.hExt = response.data.data.header.hExt
            self.faHeader.hDiscount = response.data.data.header.hDiscount
            self.faHeader.hComment = response.data.data.header.hComment
            self.faHeader.total = response.data.data.header.hTotal

            // 明細設定
            for(let i=0; i<response.data.data.entries.length; i++)
            {
                let fdata = Object.assign({}, self.faEntrys[0])
                fdata.temp = false
                fdata.accId = response.data.data.entries[i].accId
                fdata.entryTxt = response.data.data.entries[i].entryTxt
                fdata.value = response.data.data.entries[i].value
                fdata.discount = response.data.data.entries[i].discount
                fdata.ext = response.data.data.entries[i].ext
                fdata.remarks = response.data.data.entries[i].remarks
                fdata.entrySum = response.data.data.entries[i].entrySum
                fdata.reverse = response.data.data.entries[i].reverse
                self.faEntrys.push(fdata)
            }
        })
        .catch(function(error){
            self.errorModal = true
            self.errorCode  = 9999
            return
        })
    },

    data(){
        return {
            moduleName : 'FA',
            viewName : '家計簿伝票番号：',
            errorModal : false,
            errorCode : 9999,

            // ------------------------------------
            // FAヘッダ通信用オブジェクト
            // ------------------------------------
            faHeader: {
                year : null,
                month : null,
                day : null,
                bank : '',
                balance : 10,
                bankPeriod : null,
                hExt : 0,
                hDiscount : 0,
                hComment : '',
                hTotal :0,
                faNo : 0
            },

            // ------------------------------------
            // FA明細通信用オブジェクト
            // ------------------------------------
            faEntrys: [
                {
                    temp : true,
                    accId: '',
                    entryTxt: '',
                    value:  0,
                    discount: null,
                    ext: null,
                    remarks: '',
                    entrySum: null,
                    reverse : false
                }
            ],

            // ------------------------------------
            // 科目マスタ
            // ------------------------------------
            accId : [
                {
                    id      : "",
                    name    : "" ,
                    type    : null
                }
            ]
        }
    },

    methods:{
        postErrorModal(){
            this.errorModal = false
            window.close()
            return
        }
    }


}
</script>

<style scoped>
    .fainputButton[data-v-fadetail]{
        /* width:100px; */
        margin : 30px 15px;
    }

    .entry[data-v-fadetail]{
        border: solid 1px #AAAAAA;
        overflow: hidden;
        padding:10px;
        margin: 20px 0px;
        box-shadow: 0 0 6px gray;
    }

    input[data-v-fadetail]{
        width:90px;
    }

    .entryInputBlock[data-v-fadetail]{
        float:left;
        padding:5px;
    }

    p.formLabel[data-v-fadetail]{
        margin-top:10px;
        margin-bottom: 5px;
    }

    input.commentInput[data-v-fadetail]{
        width:250px;
    }

    table[data-v-fadetail]{
        margin: 10px 0px;
    }
</style>




