<template>
    <section>
        <!-- 画面ヘッダ -->
        <ViewHeader 
            v-bind:moduleName   =   'moduleName'
            v-bind:viewName     =   'viewName'
        /> 
        
        <!-- 家計簿入力ヘッダ -->
        <FAheader 
            v-bind:header = 'faHeader'
        />

        <div class='buttonBase' v-on:click = "search">検索</div>

        <!-- 家計簿伝票検索結果 -->
        <FaSearchResult v-bind:searchResult = 'faSearchResult' v-if="resultComp"/>

        <!-- 登録失敗モーダル -->
        <Modal
            v-show="errorModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event = "postErrorModal"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                <p>検索エラー</p>
                <p>(コード：{{errorCode}})</p>
            </template>
        </Modal>
        
        <Loading v-show="loading" />
    </section>
</template>

<script>
import axios    from 'axios';
import Cookies  from 'js-cookie';

import apiUrls  from '../mixins/ApiUrlMixin'

import ViewHeader   from '../components/ViewHeader'
import FAheader     from '../components/FAheader'
import FAcloseInput from '../components/FAcloseInput'
import Modal        from '../components/Modal'
import Loading      from '../components/Loading'
import FaSearchResult from '../components/faSearchResult'

export default {
    name : 'FAsearch',
    mixins : [apiUrls],

    // ------------------------------------
    //  使用コンポーネント一覧
    // ------------------------------------
    components : {
        ViewHeader,
        FAheader,
        Modal,
        Loading,
        FaSearchResult
    },

    // ------------------------------------
    //  初期化処理
    // ------------------------------------
    created : function(){

        // FAヘッダに現在日付を設定
        let dt = new Date()
        this.faHeader.year  = dt.getFullYear()
        this.faHeader.month = dt.getMonth()+1
        this.faHeader.day   = dt.getDate()
    },

    // ------------------------------------
    //  データ
    // ------------------------------------
    data(){
        return {
            moduleName : 'FA',
            viewName : '家計簿伝票検索',

            loading     : false,
            errorModal  : false,
            resultComp  : false,

            errorMsg : '',
            errorCode : '',

            // ------------------------------------
            // FAヘッダ通信用オブジェクト
            // ------------------------------------
            faHeader: {
                year : null,
                month : null,
                day : null,
                bank : '',
                balance : 10,
                bankPeriod : null
            },
            // ------------------------------------
            // FA家計簿検索結果一覧通信用オブジェクト
            // ------------------------------------
            faSearchResult : [
                {
                    faId    : null,
                    faNo    : null,
                    year    : null,
                    month   : null,
                    day     : null,
                    postDate: "",
                    bankName: "",
                    value   : null,
                    url     : ""    
                }
            ]

        }
    },

    // ------------------------------------
    //  Methods
    // ------------------------------------
    methods:{
        // ------------------------------------
        //  家計簿検索API call
        // ------------------------------------
        search(){
            // ローディング
            this.loading = true

            const self   = this

            let token = Cookies.get('token')
            if(token == null)
            {
                return
            }

            // リクエストパラメータ編集
            let reqBank  = null
            let reqYear  = null
            let reqMonth = null
            let reqDay   = null

            if(Number(this.faHeader.bank) != 0)
            {
                reqBank = Number(this.faHeader.bank)
            }
            if(Number(this.faHeader.year) != 0)
            {
                reqYear = Number(this.faHeader.year)
            }
            if(Number(this.faHeader.month) != 0)
            {
                reqMonth = Number(this.faHeader.month)
            }
            if(Number(this.faHeader.day) != 0)
            {
                reqDay = Number(this.faHeader.day)
            }

            //  FA家計簿伝票検索API
            axios.post(this.apiUrls["search"], 
                {
                    bank    : reqBank,
                    year    : reqYear,
                    month   : reqMonth,
                    day     : reqDay,
                },
                {
                    headers : {
                        'Authorization' : token
                    }
                }
            )
            .then(function(response){
                self.loading    = false
                // HTTP call success
                if(response.status == 200)
                {
                    // API success
                    if(response.data.status == 'S')
                    {
                        if(response.data.data.count <= 0)
                        {
                            self.resultComp = false
                            return
                        }
                        
                        self.setSearchResult(response.data.data.list)
                        self.resultComp = true
                        return
                    }
                    // API error
                    self.errorCode  = response.data.code
                    self.errorModal = true
                    return
                }
                // HTTP error
                self.errorModal = true
            })
            .catch(function(error){
                console.log(error)
                self.errorCode  = 9999
                self.loading    = false
                self.errorModal = true
            })
            
        },

        // ------------------------------------
        //  検索結果編集
        // ------------------------------------
        setSearchResult(resList){
            
            // 検索結果配列をクリア
            this.faSearchResult.length = 1

            for(let i=0; i<resList.length; i++)
            {
                // テンプレート用オブジェクトをコピー
                let data = Object.assign({}, this.faSearchResult[0])
                // 値設定
                data.faId    = resList[i].faId
                data.faNo    = resList[i].faNo
                data.year    = resList[i].year
                data.month   = resList[i].month
                data.day     = resList[i].day
                data.postDate= resList[i].postDate.substring(0,4) + "/" +  resList[i].postDate.substring(4,6) + "/" +  resList[i].postDate.substring(6,8)
                data.bankName= resList[i].bankName
                data.value   = Number(resList[i].value).toLocaleString( undefined, { maximumFractionDigits: 20 })
                data.url     = location.pathname + "?pId=fa06&faNo=" + resList[i].faNo

                this.faSearchResult.push(data)
            }
            this.faSearchResult.shift()

            return
        },

        // ------------------------------------
        //  エラーモーダル閉じる
        // ------------------------------------
        postErrorModal(){
            this.errorModal = false
            return
        },
    }


}
</script>




