<template>
    <div class='entry' data-v-facloseinput>
        <form data-v-facloseinput>
            <div class='entryInputBlock' data-v-facloseinput>
                <p data-v-faentry>指定月</p>
                <input type="text" name="periodInput" v-model="bankPeriod">
            </div>
            <div class='entryInputBlock' data-v-facloseinput>
                <p data-v-faentry>状態</p>
                <input type="text" name="status" v-model="status" readonly>
            </div>
        </form>
        <div class='entrySummary' data-v-facloseinput>
            <div class="buttonBase compButton" data-v-facloseinput v-on:click = "refreshData">最新情報に更新</div>
            <div class="buttonBase compButton" data-v-facloseinput v-on:click = "simulateClose" v-show="isOpen">締め処理シミュレーション</div>
            <div class="buttonBase compButton" data-v-facloseinput v-on:click = "executeClose" v-show="isOpen">締め処理実行</div>
            <div class="buttonBase compButton" data-v-facloseinput v-on:click = "reOpen" v-show="enableRepoen">再オープン</div>
        </div>
        <Modal
            v-if="errorModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event = "posterrorModal"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                <p>{{errorMsg}}</p>
                <p>(コード：{{errorCode}})</p>
            </template>
        </Modal>
        <Modal
            v-if="completeModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event = "postModal"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                <p>{{successMsg}}</p>
                <p>月末残高：{{endBalance}}</p>
            </template>
        </Modal>

        <Loading v-if="loading" />
    </div>
    
</template>

<script>
import axios    from 'axios';
import Cookies  from 'js-cookie';

import apiUrls  from '../mixins/ApiUrlMixin'
import Modal        from '../components/Modal'
import Loading      from '../components/Loading'

export default {
    name : 'FAcloseInput',

    props:['bid', 'bankPeriod'],
    mixins : [apiUrls],

    // ------------------------------------
    //  使用コンポーネント一覧
    // ------------------------------------
    components : {
        Modal,
        Loading
    },

    data(){
        return{
            period : null,
            status : null,
            isOpen : false,
            enableRepoen : false,

            preBID : null,

            errorModal: false,
            errorCode : '',
            errorMsg  : '',

            successMsg : '',
            endBalance : 0,

            completeModal : false,
            loading : false
        }
    },
    

    // ------------------------------------
    //  初期化処理
    // ------------------------------------
    updated()
    {
        if(this.bid == null || this.bid == this.preBID)
        {
            return
        }
        this.preBID = this.bid
                
        this.getBankCond()
    },
    
    // ------------------------------------
    //  Methods
    // ------------------------------------
    methods:
    {
        // ------------------------------------
        //  締め処理実行
        // ------------------------------------
        executeClose(){
            // トークン取得
            let token = Cookies.get('token')
            if(token == null)
            {
                return
            }
            
            if(this.bankPeriod.length != 7)
            {
                this.errorMsg   = '指定月はYYYY/MM形式で入力してください。'
                this.errorCode  = '9999'
                this.errorModal = true
                return
            }

            // periodを分割
            let year  = Number(this.bankPeriod.substr(0,4))
            let month = Number(this.bankPeriod.substr(5,2))

            // thisを格納
            const self = this

            this.loading = true
            axios.post(this.apiUrls["close"], 
                {
                    bank  : this.bid,
                    year  : year,
                    month : month
                },
                {
                    headers : {
                        'Authorization' : token
                    }
                }
            )
            .then(function(response){
                self.loading    = false
                // HTTP call success
                if(response.status == 200)
                {
                    // API success
                    if(response.data.status == 'S')
                    {
                        self.successMsg     = "月次締め処理が完了しました。"
                        self.endBalance     = response.data.data.endbalance
                        self.completeModal  = true
                        return
                    }
                    // API error
                    self.errorCode  = response.data.code
                    self.errorMsg   = '締め処理に失敗しました。'
                    self.errorModal = true
                    return
                }
                // HTTP error
                self.errorModal = true
                self.errorMsg   = 'alissサービスに接続できません。<br>時間をおいてから再度お試しください。'
                self.errorCode  = '9999'
            })
            .catch(function(error){
                self.loading    = false
                self.errorMsg   = 'システムエラー'
                self.errorCode  = '9999'
                self.errorModal = true
            })
        },

        // ------------------------------------
        //  締め処理シミュレーション
        // ------------------------------------
        simulateClose(){
            // トークン取得
            let token = Cookies.get('token')
            if(token == null)
            {
                return
            }
            
            if(this.bankPeriod.length != 7)
            {
                this.errorMsg   = '指定月はYYYY/MM形式で入力してください。'
                this.errorCode  = '9999'
                this.errorModal = true
                return
            }

            // periodを分割
            let year  = Number(this.bankPeriod.substr(0,4))
            let month = Number(this.bankPeriod.substr(5,2))

            // thisを格納
            const self = this

            this.loading = true
            axios.post(this.apiUrls["close"], 
                {
                    bank  : this.bid,
                    year  : year,
                    month : month,
                    simulate : true
                },
                {
                    headers : {
                        'Authorization' : token
                    }
                }
            )
            .then(function(response){
                self.loading    = false
                // HTTP call success
                if(response.status == 200)
                {
                    // API success
                    if(response.data.status == 'S')
                    {
                        self.successMsg     = "月次締め処理シミュレーションに成功しました。"
                        self.endBalance     = response.data.data.endbalance
                        self.completeModal  = true
                        return
                    }
                    // API error
                    self.errorCode  = response.data.code
                    self.errorMsg   = '締めシミュレーションに失敗しました。'
                    self.errorModal = true
                    return
                }
                // HTTP error
                self.errorModal = true
                self.errorMsg   = 'alissサービスに接続できません。<br>時間をおいてから再度お試しください。'
                self.errorCode  = '9999'
            })
            .catch(function(error){
                self.loading    = false
                self.errorMsg   = 'システムエラー'
                self.errorCode  = '9999'
                self.errorModal = true
            })
        },

        // ------------------------------------
        //  再オープン
        // ------------------------------------
        reOpen(){
        },

        // ------------------------------------
        //  最新情報に更新
        // ------------------------------------
        refreshData(){
            this.getBankCond()
        },

         // ------------------------------------
        //  登録完了モーダル閉じる
        // ------------------------------------
        postModal(){
            this.completeModal = false
            // location.reload()
            window.scrollTo(0,0)
            return
        },

        // ------------------------------------
        //  エラーモーダル閉じる
        // ------------------------------------
        posterrorModal(){
            this.errorModal = false
            return
        },

        // ------------------------------------
        //  口座情報取得内部メソッド
        // ------------------------------------
        getBankCond(){            
            // トークン取得
            let token = Cookies.get('token')
            if(token == null)
            {
                return
            }
            
            if(this.bankPeriod.length != 7)
            {
                this.errorMsg   = '指定月はYYYY/MM形式で入力してください。'
                this.errorCode  = '9999'
                this.errorModal = true
                return
            }

            // periodを分割
            let year  = Number(this.bankPeriod.substr(0,4))
            let month = Number(this.bankPeriod.substr(5,2))

            // thisを格納
            const self = this

            // Bank状況取得API call
            let url = this.apiUrls["getBankCond"] + '/' + this.bid + '/' + year + '/' + month

            this.loading = true
            axios.get(url,
            {
                headers : {
                    'Authorization' : token
                }
            })
            .then(function(response){
                // Http status not 200
                if(response.status != 200)
                {
                    self.loading    = false
                    self.errorMsg   = 'alissサービスに接続できません。<br>時間をおいてから再度お試しください。'
                    self.errorCode  = '9999'
                    self.errorModal = true
                    return
                }
                // Auth failed
                if(response.data.status != 'S')
                {
                    self.loading    = false
                    self.errorMsg   = '締め対象期間の口座情報取得に失敗しました。'
                    self.errorCode  = response.data.code
                    self.errorModal = true
                    return
                }

                // 口座のOpen/Close
                if(response.data.data.status == 0)
                {
                    self.status = 'Open'
                    self.isOpen = true
                }
                else
                {
                    self.status = 'Closed'
                    self.isOpen = false
                }

                // 口座の再オープン可否
                self.enableRepoen = response.data.data.enableRepoen

                self.loading    = false

            })
            .catch(function(error){
                self.loading    = false
                self.errorMsg   = 'システムエラー'
                self.errorCode  = '9999'
                self.errorModal = true
                return
            })

        }
    }

}
</script>


<style scoped>
    .entry[data-v-facloseinput]{
        border: solid 1px #AAAAAA;
        overflow: hidden;
        padding:10px;
        margin: 20px 0px;
        box-shadow: 0 0 6px gray;
    }
    .entryInputBlock[data-v-facloseinput]{
        float:left;
        padding:5px;
    }
    .entrySummary[data-v-facloseinput]{
        clear:left;
        padding:5px;
    }

    .compButton[data-v-facloseinput]{
        margin : 15px 0px;
    }

</style>


