<template>
    <section>
        <!-- 画面ヘッダ -->
        <ViewHeader 
            v-bind:moduleName   =   'moduleName'
            v-bind:viewName     =   'viewName'
        /> 
        <!-- 家計簿入力ヘッダ -->
        <FAheader 
            v-bind:header = 'faHeader'
            v-on:header-bank-send-event = 'recieveBank'
        />

        <!-- 振替入力ブロック -->
        <section>
            <div class='entry' data-v-fatransfer>
                <div class='entryInputBlock' data-v-fatransfer>
                    <p data-v-faentry>振替先口座ID*</p>
                    <select name="dBank" v-model="dest.bank" v-on:change="changeDestBank" style="width:180px;">
                        <option v-for='(bank, index) in bankInfo' :key='index' v-bind:value="bank.id">
                            {{bank.name}}
                        </option>
                    </select>
                </div>
                <div class='entryInputBlock' data-v-fatransfer>
                    <p data-v-faentry>振替金額*</p>
                    <input type="number" name="transferValueInput" v-model="transferValue">
                </div>
                <div class='entryInputBlock' data-v-fatransfer>
                    <p data-v-faentry>振替元科目*</p>
                    <select name="sAccInput" v-model="source.accId">
                        <option v-for='(account, index) in accId' :key='index' v-bind:value="account.id">{{account.id}}：{{account.name}}</option>
                    </select>
                </div>
                <div class='entryInputBlock' data-v-fatransfer>
                    <p data-v-faentry>振替元摘要コメント</p>
                    <input type="text" name="sCommentInput" v-model="source.comment">
                </div>
                <div class='entryInputBlock' data-v-fatransfer>
                    <p data-v-faentry>振替先科目*</p>
                    <select name="dAccInput" v-model="dest.accId">
                        <option v-for='(account, index) in accId' :key='index' v-bind:value="account.id">{{account.id}}：{{account.name}}</option>
                    </select>
                </div>
                <div class='entryInputBlock' data-v-fatransfer>
                    <p data-v-faentry>振替先摘要コメント</p>
                    <input type="text" name="dCommentInput" v-model="dest.comment">
                </div>
            </div>

            <div class='entrySummary' data-v-fatransfer>
                <div class="buttonBase compButton" data-v-facloseinput v-on:click = "postTransfer">振替転記</div>
            </div>

        </section>

        <!-- 伝票金額 -->
        <section>
            <div class='entry' data-v-fatransfer>
                <table  data-v-fatransfer>
                    <tr><td colspan='3'>現残高</td></tr>
                    <tr><td width='15px'></td><td>振替元：</td><td>{{faHeader.balance}}</td></tr>
                    <tr><td></td><td>振替先：</td><td>{{dest.balance}}</td></tr>
                    <tr><p /></tr>
                    <tr><td colspan='3'>振替後残高</td></tr>
                    <tr><td width='10px'></td><td>振替元：</td><td>{{ Number(faHeader.balance) - Number(transferValue)}}</td></tr>
                    <tr><td></td><td>振替先：</td><td>{{ Number(dest.balance) + Number(transferValue)}}</td></tr>
                </table>
            </div>
        </section>

        <!-- 登録確認用モーダル -->
        <Modal
            v-if="confirmModal"
            v-bind:mtypeIn='1'
            v-on:modal-yes-event    = "confirmYes"
            v-on:modal-no-event     = "confirmNo"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                <table  data-v-fainput>
                    <p>振替を実行しますか？</p>
                    <p>振替金額：{{transferValue}}</p>
                </table>
            </template>
        </Modal>

        <!-- 登録完了モーダル -->
        <Modal
            v-if="completeModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event = "postModal"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                <p>振替伝票を登録しました。</p>
                <p>振替伝票番号：{{faTransferNo}}</p>
            </template>
        </Modal>
        <!-- 登録失敗モーダル -->
        <Modal
            v-show="errorModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event = "postErrorModal"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                <p>振替伝票登録に失敗しました。</p>
                <p>(コード：{{errorCode}})</p>
            </template>
        </Modal>
        

        <Loading v-if="loading" />

    </section>
</template>

<script>
import axios    from 'axios';
import Cookies  from 'js-cookie';

import apiUrls  from '../mixins/ApiUrlMixin'

import ViewHeader   from '../components/ViewHeader'
import FAheader     from '../components/FAheader'
import FAcloseInput from '../components/FAcloseInput'
import Modal        from '../components/Modal'
import Loading      from '../components/Loading'


export default {
    name : 'FAclose',
    mixins : [apiUrls],
    
    // ------------------------------------
    //  使用コンポーネント一覧
    // ------------------------------------
    components : {
        ViewHeader,
        FAheader,
        Modal,
        Loading
    },

    // ------------------------------------
    //  初期化処理
    // ------------------------------------
    created : function(){

        // FAヘッダに現在日付を設定
        let dt = new Date()
        this.faHeader.year  = dt.getFullYear()
        this.faHeader.month = dt.getMonth()+1
        this.faHeader.day   = dt.getDate()

        //  科目取得処理
        // トークン取得
        let token = Cookies.get('token')
        if(token == null)
        {
            return
        }

        // thisを格納
        const self = this

        // 科目取得API call
        axios.get(this.apiUrls["getAllAccounts"],
        {
            headers : {
                'Authorization' : token
            }
        })
        .then(function(response){
            // Http status not 200
            if(response.status != 200)
            {
                return
            }
            // Auth failed
            if(response.data.status != 'S')
            {
                return
            }
            
            // 取得成功時
            for(let i=0; i<response.data.data.length; i++)
            {
                // テンプレート用オブジェクトをコピー
                let data = Object.assign({}, self.accId[0])
                
                // 値設定
                data.id     = response.data.data[i].accid
                data.name   = response.data.data[i].name
                data.type   = response.data.data[i].accType
                // 明細オブジェクト追加
                self.accId.push(data)
            }

            // テンプレート用オブジェクト削除
            self.accId.shift()

            // 科目初期値設定
            self.setDefaultAccId()
        })
        .catch(function(error){
            return
        })

    },

    // ------------------------------------
    //  データ
    // ------------------------------------
    data(){
        return {
            moduleName : 'FA',
            viewName : 'Bank間振替',

            // ------------------------------------
            // FAヘッダ通信用オブジェクト
            // ------------------------------------
            faHeader: {
                year : null,
                month : null,
                day : null,
                bank : '',
                balance : 0,
                bankPeriod : null
            },

            // ------------------------------------
            // 振替元情報入力オブジェクト
            // ------------------------------------
            source : {
                bank : 0,
                accId : '',
                comment : '',
                balance : 0
            },

            // ------------------------------------
            // 振替先情報入力オブジェクト
            // ------------------------------------
            dest : {
                bank : 0,
                accId : '',
                comment : '',
                balance : 0
            },

            transferValue : null,

            // ------------------------------------
            // Bankマスタ
            // ------------------------------------
            bankInfo:[
                    {
                        id      : "",
                        name    : "",
                        balance : 0
                    }
                ],

            // ------------------------------------
            // 科目マスタ
            // ------------------------------------
            accId : [
                {
                    id      : "",
                    name    : "" ,
                    type    : null
                }
            ],
            
            confirmModal : false,
            loading : false,
            completeModal : false,
            errorModal : false,

            faTransferNo : 111,
            errorCode : 9999,
        }
    },
    // ------------------------------------
    //  Methods
    // ------------------------------------
    methods:{
        postTransfer(){
            this.confirmModal = true
        },

        // ------------------------------------
        // 登録確認モーダル Yes押下
        // ------------------------------------
        confirmYes (){
            // モーダル閉じる
            this.confirmModal = false

            //  振替元と振替先が同一
            if (this.faHeader.bank == this.dest.bank )
            {
                this.errorCode  = 9210
                this.errorModal = true
                return
            }

            // 振替元・振替先未設定
            if(this.faHeader.bank == '' || this.dest.bank == '')
            {
                this.errorCode  = 9211
                this.errorModal = true
                return
            }

            // 振替金額不正
            if(this.transferValue <= 0 || this.transferValue == null)
            {
                this.errorCode  = 9212
                this.errorModal = true
                return
            }

            // loading
            this.loading = true

            // 振替登録API呼出
            this.callTransferApi()
        },

        // ------------------------------------
        // Bank間振替API呼出
        // ------------------------------------
        callTransferApi(){
            const self = this

            let req = {
                header : {
                    year :  this.faHeader.year,
                    month : this.faHeader.month,
                    day :   this.faHeader.day,
                    value : this.transferValue
                },
                source : {
                    bank :      this.faHeader.bank,
                    accId :     this.source.accId,
                    comment :   this.source.comment
                },
                dest : {
                    bank :      this.dest.bank,
                    accId :     this.dest.accId,
                    comment :   this.dest.comment
                }
            }
            console.log(JSON.stringify(req))

            // トークン取得
            let token = Cookies.get('token')
            if(token == null)
            {
                return
            }

            axios.post(this.apiUrls["transfer"],
            {
                header : {
                    year :  this.faHeader.year,
                    month : this.faHeader.month,
                    day :   this.faHeader.day,
                    value : this.transferValue
                },
                source : {
                    bank :      this.faHeader.bank,
                    accId :     this.source.accId,
                    comment :   this.source.comment
                },
                dest : {
                    bank :      this.dest.bank,
                    accId :     this.dest.accId,
                    comment :   this.dest.comment
                }
            },
            {
                headers : {
                    'Authorization' : token
                }
            })
            .then(function(response){
                self.loading = false

                // Http status not 200
                if(response.status != 200)
                {
                    self.errorCode  = 9999
                    self.errorModal = true
                    return
                }
                // APIfailed
                if(response.data.status != 'S')
                {
                    self.errorCode  = response.data.code
                    self.errorModal = true
                    return
                }

                // 登録成功
                self.faTransferNo  = response.data.data.transferNo
                self.completeModal = true
            })
            .catch(function(error){
                self.loading    = false
                self.errorCode  = 9999
                self.errorModal = true
                return
            })
        },

        // ------------------------------------
        // 登録確認モーダル No押下
        // ------------------------------------
        confirmNo (){
            // モーダル閉じる
            this.confirmModal = false
        },

        // ------------------------------------
        // 家計簿登録後モーダルOK
        // ------------------------------------
        postModal(){
            this.completeModal = false
            location.reload()
            window.scrollTo(0,0)
            return
        },
        postErrorModal(){
            this.errorModal = false
            return
        },

        // ------------------------------------
        //  Bankマスタ連携
        // ------------------------------------
        recieveBank(banks){

            for(let i=1; i<banks.length; i++)
            {
                let data = Object.assign({}, this.bankInfo[0])
                data.id = banks[i].id
                data.name = banks[i].name
                data.balance = banks[i].balance
                this.bankInfo.push(data)
            }
        },

        // ------------------------------------
        //  振替先口座変更イベント
        // ------------------------------------
        changeDestBank(){
            for(let i=0; i<this.bankInfo.length; i++)
            {
                if(this.bankInfo[i].id == this.dest.bank)
                {
                    this.dest.balance = this.bankInfo[i].balance
                }
            }
        },

        // ------------------------------------
        //  科目初期値設定
        // ------------------------------------
        setDefaultAccId(){
            const self = this

            // 振替元科目取得API call
            // トークン取得
            let token = Cookies.get('token')
            if(token == null)
            {
                return
            }
            // URL生成
            let sAccConfig = this.apiUrls["config"] + '2001/1/'
            axios.get(sAccConfig,
            {
                headers : {
                    'Authorization' : token
                }
            })
            .then(function(response){
                // Http status not 200
                if(response.status != 200)
                {
                    return
                }
                // Auth failed
                if(response.data.status != 'S')
                {
                    return
                }
                self.source.accId = response.data.data.configStr
            })
            .catch(function(error){
                return
            })

            // 振替先科目取得API call
            // URL生成
            let dAccConfig = this.apiUrls["config"] + '2001/2/'
            axios.get(dAccConfig,
            {
                headers : {
                    'Authorization' : token
                }
            })
            .then(function(response){
                // Http status not 200
                if(response.status != 200)
                {
                    return
                }
                // Auth failed
                if(response.data.status != 'S')
                {
                    return
                }
                self.dest.accId = response.data.data.configStr
            })
            .catch(function(error){
                return
            })
        }
    }
}
</script>

<style scoped>
    .entry[data-v-fatransfer]{
        border: solid 1px #AAAAAA;
        overflow: hidden;
        padding:10px;
        margin: 20px 0px;
        box-shadow: 0 0 6px gray;
    }

    .entryInputBlock[data-v-fatransfer]{
        float:left;
        padding:5px;
    }

    .entrySummary[data-v-fatransfer]{
        clear:left;
        padding:5px;
    }

    p.formLabel[data-v-fatransfer]{
        margin-top:10px;
        margin-bottom: 5px;
    }

    input.commentInput[data-v-fatransfer]{
        width:250px;
    }

    table[data-v-fatransfer]{
        margin: 10px 0px;
    }
</style>


