<template>
    <transition name="fasearchresult">
    <div class='entry' data-v-searchResult>
        <h1 data-v-searchResult>検索結果</h1>
        <div class="portalReport" data-v-searchResult>
            <table width='100%' data-v-searchResult>
                <thead data-v-searchResult>
                <tr>
                    <th width='20%' data-v-searchResult>伝票番号</th>
                    <th width='20%' data-v-searchResult>転記日</th>
                    <th width='30%' data-v-searchResult>口座</th>
                    <th width='30%' data-v-searchResult>金額</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for='(result, index) in searchResult' :key='index'>
                    <td><a v-bind:href="result.url" target='_blank'>{{result.faNo}}</a></td>
                    <td>{{result.postDate}}</td>
                    <td>{{result.bankName}}</td>
                    <td>{{result.value}}</td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
    </transition>
</template>

<script>
export default {
    name : 'FAsearchResult',

    props : ["searchResult"],
}
</script>

<style scoped>
    p[data-v-searchResult]
    {
        margin : 10px 20px;
    }

    h1[data-v-searchResult]{
        margin : 5px 10px;
        font-size: 120%;
        font-weight: 400;
        color: #f16272;
    }

    .entry[data-v-searchResult]{
        border: solid 1px #AAAAAA;
        overflow: hidden;
        padding:10px;
        margin: 20px 0px;
        box-shadow: 0 0 6px gray;
    }

    .portalReport[data-v-searchResult]{
        width:95%;
        margin: 0 auto;
    }

    table, th,td, tr[data-v-searchResult]{
        text-align: left;
        padding: 5px 10px;
        border: 1px solid #AAAAAA;
        border-collapse: collapse;
        font-weight: normal;
        margin : 15px 0px 25px 0px;
    }

    thead[data-v-searchResult]{
        background-color: #999999;
        color: #ffffff;
    }

    #appTitle[data-v-searchResult]{
        font-size:400%;
        font-family: 'Didact Gothic';
        text-align: center;
        color: #f16272;

        padding :10% 0px;
    }

    .fasearchresult-enter-active, .fasearchresult-leave-active{
        transition: opacity 0.3s;
    }
    .fasearchresult-enter{
        opacity: 0;
    }
    .fasearchresult-leave-to{
        opacity: 0;
    }
</style>


